.petpost {
    background-color: white;
    border-radius: 5px;
    margin: 10px 0;
    border: 1px solid #dedbdb;
}

.petpost .petpost-header {
    padding: 5px 10px;
    display: flex;
    border-bottom: 1px solid #dedbdb;
}

.petpost .petpost-body {
    /* min-height: 40vh; */
}

.petpost .petpost-body p {
    margin: 0 0 10px 0;
    padding: 5px 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.petpost .petpost-body img {
    width: 100%;
    max-height: 70vh;
}
