.section {
    width: 30vw;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    margin-top: 8vh;
    border-left: 1px solid #e4e4e4;
    background-color: #FAFAFA;
}

.section .padding {
    padding: 10px;
}

.section .title {
    margin-top: 0;
    margin-bottom: 10px;
    font-weight: 600;
    color: #2e2d2dbf;
}

.section .news-item {
    display: flex;
    padding: 5px 5px 10px;
    cursor: pointer;
    border-radius: 5px;
}

.section .news-item:hover {
    background-color: #e7e7e7;
}

.section .news-item .user-avatar {
    height: 5vh;
    margin-top: 5px;
}

.section .news-item .news-text {
    margin-left: 10px;
}

.section .news-item .news-text p {
    margin: 0;
}

.news-item ~ .news-item {
    border-top: 2px solid #f0efef;
}
