.login-page {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgb(250, 250, 250);
}

.login-page .body {
    position: absolute;
    top: 10vh;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: auto;
    padding: 20px 40px;
    display: flex;
    justify-content: center;
}

.center-wrapper {
    margin-top: auto;
    margin-bottom: auto;
    min-width: 45vw;
    max-width: 75vw;
}

.center-wrapper.md {
    min-width: 40vw;
    max-width: 50vw;
}

.center-wrapper.xl {
    min-width: 25vw;
    max-width: 30vw;
}

.card {
    border: 1px solid #cbcbcb;
    padding: 20px;
    border-radius: 2px;
}

.card.card-login {
    text-align: center;
}

.card-login .buttons {
    padding: 15px 0px;
}

.card-login h2 {
    margin-top: 0;
}

.card-singup {
    margin: 15px 0;
    text-align: center;
}
