.petpost-header .user-avatar {
    height: 5vh;
    width: auto;
    border-radius: 50%;
    margin: 5px 10px 5px 0;
    border: 1px solid #f3f1f1;
}

.petpost-header .petpost-text {
    margin-top: auto;
    margin-bottom: auto;
    width: 100%;
}

.petpost-header .petpost-text label,
.petpost-header .petpost-text small {
    display: block;
}

.petpost-header .petpost-text small {
    font-size: 13px;
    color: #787e83;
}

.petpost-header .petpost-more {
    display: flex;
}

.petpost-header .petpost-more button {
    margin-top: auto;
    margin-bottom: auto;
}
