.simple-gallery {
    margin-top: 10px;
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: repeat(2, 150px);
    grid-gap: 15px;
}

.simple-gallery.div-1 .simple-gallery-image:nth-child(1) {
    grid-column: 1 / 3;
    grid-row: 1 / 3;
}

.simple-gallery.div-2 .simple-gallery-image:nth-child(1) {
    grid-column: 1 / 2;
    grid-row: 1 / 3;
}

.simple-gallery.div-2 .simple-gallery-image:nth-child(2) {
    grid-column: 2 / 2;
    grid-row: 1 / 3;
}

.simple-gallery.div-3 .simple-gallery-image:nth-child(1) {
    grid-column: 1 / 2;
    grid-row: 1 / 3;
}

.simple-gallery-image {
    overflow: hidden;
    position: relative;
    border: 1px solid #e1dfdf;
    border-radius: 15px;
}

.simple-gallery-image button {
    position: absolute;
    top: 0;
    left: 0;
    background: transparent;
    border: none;
    cursor: pointer;
    border-radius: 50%;
    margin: 10px;
    height: 40px;
    width: 40px;
    background-color: #9696965e;
}

.simple-gallery-image button:hover {
    background-color: #969696b0;
}

.simple-gallery-image div.img {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
