.navbar-item {
    margin: 5px;
    padding: 5px;
    display: flex;
    border-radius: 10px;
    cursor: pointer;
}

.navbar-item:hover {
    background-color: #0458c0;
}

.navbar-item label {
    margin-top: auto;
    margin-bottom: auto;
    color: white;
    cursor: pointer;
    font-size: 16px;
    line-height: 25px;
    margin-left: 5px;
}
