.navbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    background-color: #1771e1;
    height: 8vh;
    padding: 0 20px 0 20px;
    z-index: 1;
}

.navbar .navbar-items {
    display: inline-flex;
    margin: auto 0 auto auto;
}

.navbar .navbar-items .avatar {
    width: auto;
    height: 5vh;
    border-radius: 50%;
}
