.sidebar {
    width: 25vw;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 0;
    margin-top: 8vh;
    border-right: 1px solid #e4e4e4;
    background-color: #FAFAFA;
}

.sidebar .padding {
    padding: 10px;
}

.sidebar ul {
    padding: 0;
}

.sidebar ul li {
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
    margin-top: 5px;
    margin-bottom: 5px;
    display: flex;
    color: #585555;
}

.sidebar ul li.active,
.sidebar ul li:hover {
    background-color: #c5cfed;
    color: #1771e1;
}
.sidebar ul li.active a,
.sidebar ul li:hover a {
    color: #1771e1;
}

.sidebar ul li a {
    margin-top: auto;
    margin-bottom: auto;
    font-size: 16px;
    font-weight: 500;
    margin-left: 10px;
    color: #585555;
}

.sidebar .separator {
    content: "";
    border-bottom: 1px solid #d9d6d6;
    margin: 0 5px;
}

.more {
    font-size: 13px;
    padding: 5px;
    text-align: center;
}
