.petpost-new {
    border: 1px solid #d8d8d8;
    padding: 20px 20px 10px 20px;
    background-color: white;
    border-radius: 5px;
}

.petpost-new .content {
}

.petpost-new textarea {
    width: 100%;
    resize: none;
    border: none;
    margin: 0;
    padding: 0;
    outline: none;
    font-size: 16px;
    line-height: 18px;
    overflow: hidden;
}

.petpost-new textarea::-webkit-input-placeholder {
    font-size: 17px;
}

.petpost-new .footer {
    display: grid;
    grid-template-columns: max-content auto max-content;
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px solid #d8d8d8;
}

.petpost-new .footer .options {
    display: flex;
}

.petpost-new .footer .counter-chars {
    margin: 0 10px;
    color: #1771e1;
}

.petpost-new .footer .counter-chars.limit {
    color: red;
}
