.petpost-photos {
}

.petpost-photos .wrapper {
    display: flex;
    min-height: 50vh;
    max-height: 65vh;
    margin-bottom: 10px;
    justify-content: center;
}

.petpost-photos .wrapper .photo {
    width: 100%;
    display: flex;
    /* background: url("") no-repeat center center; */
    background: no-repeat center center;
    background-size: contain;
}

/* .petpost-photos .wrapper .photo img {
    width: auto;
    height: 100%;
    margin: auto;
} */

.dots {
    display: flex;
    justify-content: center;
}

.dots .dot {
    height: 10px;
    width: 10px;
    background-color: #c7b7a5;
    margin: auto 5px;
    border-radius: 50%;
    cursor: pointer;
}

.dots .dot.large:nth-child(1) {
    height: 7px;
    width: 7px;
}

.dots .dot.selected {
    background-color: #1771e1;
}
