.hidden {
    display: none;
}

.dropdown {
    position: absolute;
    top: 65px;
    border: 1px solid rgb(231 231 231);
    z-index: 2;
    background-color: white;
    text-align: left;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
li {
    padding: 8px 12px;
}

li:hover {
    background-color: rgba(0, 0, 0, 0.14);
    cursor: pointer;
}
