.brand {
    display: grid;
    grid-template-columns: min-content max-content;
    margin: auto 0 auto 0;
    border-radius: 5px;
    cursor: pointer;
}

.brand:hover {
    background-color: #0458c0;
}

.brand .brand-logo {
    height: 6vh;
    width: auto;
    border: 2px solid white;
    border-radius: 50%;
}

.brand h4 {
    color: white;
    margin: auto 5px auto 10px;
    font-size: 1.5rem;
}
