@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap");

body {
    margin: 0;
    font-family: "Lato", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

a {
    color: #0c68db;
    text-decoration: none;
    cursor: pointer;
}

body * {
    font-family: inherit;
}
