.toggle-button {
    display: inline-flex;
    margin: 0 5px 0 0;
    padding: 5px 5px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: transparent;
    color: #1771e1;
}

.toggle-button * {
    margin-top: auto;
    margin-bottom: auto;
    cursor: pointer;
}

.toggle-button:hover {
    background-color: #e9e9e9;
}

.toggle-button.disabled {
    background-color: transparent;
    cursor: inherit;
    color: gray;
}

.toggle-button.select {
    background-color: #a8caf49c;
    color: #1771e1;
}

.toggle-button label {
    margin-left: 5px;
}
