.petpost-button {
    display: inline-flex;
    margin: 0 5px 0 0;
    padding: 1px 5px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: transparent;
}

.petpost-button:hover {
    background-color: #e9e9e9;
}

.petpost-button label {
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 5px;
    cursor: pointer;
}

.petpost-button.primary {
    color: #1771e1;
}
