.btn-input-file {
    display: inline-flex;
    margin: 0 5px 0 0;
    padding: 5px 5px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: transparent;
    color: #1771e1;
}

.btn-input-file:hover {
    background-color: #e9e9e9;
}

.btn-input-file.disabled {
    background-color: transparent;
    cursor: inherit;
    color: gray;
}

.btn-input-file input[type="file"] {
    display: none;
}
