@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap);
.brand {
    display: grid;
    grid-template-columns: -webkit-min-content -webkit-max-content;
    grid-template-columns: min-content max-content;
    margin: auto 0 auto 0;
    border-radius: 5px;
    cursor: pointer;
}

.brand:hover {
    background-color: #0458c0;
}

.brand .brand-logo {
    height: 6vh;
    width: auto;
    border: 2px solid white;
    border-radius: 50%;
}

.brand h4 {
    color: white;
    margin: auto 5px auto 10px;
    font-size: 1.5rem;
}

.navbar-item {
    margin: 5px;
    padding: 5px;
    display: flex;
    border-radius: 10px;
    cursor: pointer;
}

.navbar-item:hover {
    background-color: #0458c0;
}

.navbar-item label {
    margin-top: auto;
    margin-bottom: auto;
    color: white;
    cursor: pointer;
    font-size: 16px;
    line-height: 25px;
    margin-left: 5px;
}

.hidden {
    display: none;
}

.dropdown {
    position: absolute;
    top: 65px;
    border: 1px solid rgb(231 231 231);
    z-index: 2;
    background-color: white;
    text-align: left;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
li {
    padding: 8px 12px;
}

li:hover {
    background-color: rgba(0, 0, 0, 0.14);
    cursor: pointer;
}

.navbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    background-color: #1771e1;
    height: 8vh;
    padding: 0 20px 0 20px;
    z-index: 1;
}

.navbar .navbar-items {
    display: inline-flex;
    margin: auto 0 auto auto;
}

.navbar .navbar-items .avatar {
    width: auto;
    height: 5vh;
    border-radius: 50%;
}

.banner {
    background-color: #ebebeb;
    display: flex;
    padding: 50px;
}

.banner.banner-mobile {
    display: block;
    padding: 20px;
}

.banner .slogans {
    padding: 20px;
    display: flex;
    flex: 1 1;
}

.slogans-center {
    margin-top: auto;
    margin-bottom: auto;
}

.slogans-center h1 {
    font-size: 3rem;
    margin-top: 0;
}

.slogans-center p {
    white-space: break-spaces;
}

.slogans-buttons button ~ button {
    margin-left: 15px;
}

.banner .container-image {
    padding: 20px;
    text-align: center;
    flex: 1 1;
    display: flex;
}

.container-image img {
    width: 100%;
    height: auto;
    margin: auto 0 auto 0;
}

.welcome-page {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgb(250, 250, 250);
}

.welcome-page .body {
    position: absolute;
    top: 10vh;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: auto;
}

.primary-button {
    background-color: #0c68db;
    color: white;
    border-radius: 5px;
    border: 1px solid #1771e1;
    padding: 10px 20px;
    font-size: 15px;
    font-family: unset;
}

.primary-button:hover {
    background-color: #1771e1;
    cursor: pointer;
}

.primary-button.block {
    width: 100%;
}

.primary-button.disabled {
    background-color: #0c68db70;
    border: 1px solid #0c68db70;
}

.sidebar {
    width: 25vw;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 0;
    margin-top: 8vh;
    border-right: 1px solid #e4e4e4;
    background-color: #FAFAFA;
}

.sidebar .padding {
    padding: 10px;
}

.sidebar ul {
    padding: 0;
}

.sidebar ul li {
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
    margin-top: 5px;
    margin-bottom: 5px;
    display: flex;
    color: #585555;
}

.sidebar ul li.active,
.sidebar ul li:hover {
    background-color: #c5cfed;
    color: #1771e1;
}
.sidebar ul li.active a,
.sidebar ul li:hover a {
    color: #1771e1;
}

.sidebar ul li a {
    margin-top: auto;
    margin-bottom: auto;
    font-size: 16px;
    font-weight: 500;
    margin-left: 10px;
    color: #585555;
}

.sidebar .separator {
    content: "";
    border-bottom: 1px solid #d9d6d6;
    margin: 0 5px;
}

.more {
    font-size: 13px;
    padding: 5px;
    text-align: center;
}

.btn-input-file {
    display: inline-flex;
    margin: 0 5px 0 0;
    padding: 5px 5px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: transparent;
    color: #1771e1;
}

.btn-input-file:hover {
    background-color: #e9e9e9;
}

.btn-input-file.disabled {
    background-color: transparent;
    cursor: inherit;
    color: gray;
}

.btn-input-file input[type="file"] {
    display: none;
}

.simple-gallery {
    margin-top: 10px;
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: repeat(2, 150px);
    grid-gap: 15px;
}

.simple-gallery.div-1 .simple-gallery-image:nth-child(1) {
    grid-column: 1 / 3;
    grid-row: 1 / 3;
}

.simple-gallery.div-2 .simple-gallery-image:nth-child(1) {
    grid-column: 1 / 2;
    grid-row: 1 / 3;
}

.simple-gallery.div-2 .simple-gallery-image:nth-child(2) {
    grid-column: 2 / 2;
    grid-row: 1 / 3;
}

.simple-gallery.div-3 .simple-gallery-image:nth-child(1) {
    grid-column: 1 / 2;
    grid-row: 1 / 3;
}

.simple-gallery-image {
    overflow: hidden;
    position: relative;
    border: 1px solid #e1dfdf;
    border-radius: 15px;
}

.simple-gallery-image button {
    position: absolute;
    top: 0;
    left: 0;
    background: transparent;
    border: none;
    cursor: pointer;
    border-radius: 50%;
    margin: 10px;
    height: 40px;
    width: 40px;
    background-color: #9696965e;
}

.simple-gallery-image button:hover {
    background-color: #969696b0;
}

.simple-gallery-image div.img {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.toggle-button {
    display: inline-flex;
    margin: 0 5px 0 0;
    padding: 5px 5px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: transparent;
    color: #1771e1;
}

.toggle-button * {
    margin-top: auto;
    margin-bottom: auto;
    cursor: pointer;
}

.toggle-button:hover {
    background-color: #e9e9e9;
}

.toggle-button.disabled {
    background-color: transparent;
    cursor: inherit;
    color: gray;
}

.toggle-button.select {
    background-color: #a8caf49c;
    color: #1771e1;
}

.toggle-button label {
    margin-left: 5px;
}

.petpost-new {
    border: 1px solid #d8d8d8;
    padding: 20px 20px 10px 20px;
    background-color: white;
    border-radius: 5px;
}

.petpost-new .content {
}

.petpost-new textarea {
    width: 100%;
    resize: none;
    border: none;
    margin: 0;
    padding: 0;
    outline: none;
    font-size: 16px;
    line-height: 18px;
    overflow: hidden;
}

.petpost-new textarea::-webkit-input-placeholder {
    font-size: 17px;
}

.petpost-new .footer {
    display: grid;
    grid-template-columns: -webkit-max-content auto -webkit-max-content;
    grid-template-columns: max-content auto max-content;
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px solid #d8d8d8;
}

.petpost-new .footer .options {
    display: flex;
}

.petpost-new .footer .counter-chars {
    margin: 0 10px;
    color: #1771e1;
}

.petpost-new .footer .counter-chars.limit {
    color: red;
}

.petpost-button {
    display: inline-flex;
    margin: 0 5px 0 0;
    padding: 1px 5px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: transparent;
}

.petpost-button:hover {
    background-color: #e9e9e9;
}

.petpost-button label {
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 5px;
    cursor: pointer;
}

.petpost-button.primary {
    color: #1771e1;
}

.petpost-header .user-avatar {
    height: 5vh;
    width: auto;
    border-radius: 50%;
    margin: 5px 10px 5px 0;
    border: 1px solid #f3f1f1;
}

.petpost-header .petpost-text {
    margin-top: auto;
    margin-bottom: auto;
    width: 100%;
}

.petpost-header .petpost-text label,
.petpost-header .petpost-text small {
    display: block;
}

.petpost-header .petpost-text small {
    font-size: 13px;
    color: #787e83;
}

.petpost-header .petpost-more {
    display: flex;
}

.petpost-header .petpost-more button {
    margin-top: auto;
    margin-bottom: auto;
}

.petpost-options {
    display: grid;
    grid-template-columns: -webkit-min-content -webkit-min-content auto;
    grid-template-columns: min-content min-content auto;
    padding: 10px;
}

.petpost-options .option-adoption {
    display: flex;
    justify-content: flex-end;
}

.petpost {
    background-color: white;
    border-radius: 5px;
    margin: 10px 0;
    border: 1px solid #dedbdb;
}

.petpost .petpost-header {
    padding: 5px 10px;
    display: flex;
    border-bottom: 1px solid #dedbdb;
}

.petpost .petpost-body {
    /* min-height: 40vh; */
}

.petpost .petpost-body p {
    margin: 0 0 10px 0;
    padding: 5px 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.petpost .petpost-body img {
    width: 100%;
    max-height: 70vh;
}

.petpost-photos {
}

.petpost-photos .wrapper {
    display: flex;
    min-height: 50vh;
    max-height: 65vh;
    margin-bottom: 10px;
    justify-content: center;
}

.petpost-photos .wrapper .photo {
    width: 100%;
    display: flex;
    /* background: url("") no-repeat center center; */
    background: no-repeat center center;
    background-size: contain;
}

