.dots {
    display: flex;
    justify-content: center;
}

.dots .dot {
    height: 10px;
    width: 10px;
    background-color: #c7b7a5;
    margin: auto 5px;
    border-radius: 50%;
    cursor: pointer;
}

.dots .dot.large:nth-child(1) {
    height: 7px;
    width: 7px;
}

.dots .dot.selected {
    background-color: #1771e1;
}

.input-text {
    background: #fafafa;
    width: 100%;
    margin: 0 0 10px 0;
    border: 1px solid #dbdbdb;
    border-radius: 3px;
}

.input-text.focus {
    border: 1px solid #a8a8a8;
}

.input-text label {
    display: flex;
    height: 36px;
    flex: 1 0;
    padding: 0;
    position: relative;
    margin: 0;
    min-width: 0;
}

.input-text label span {
    color: #8e8e8e;
    height: 36px;
    left: 8px;
    line-height: 36px;
    overflow: hidden;
    pointer-events: none;
    position: absolute;
    right: 0;
    text-overflow: ellipsis;
    -webkit-transform-origin: left;
    transform-origin: left;
    transition: -webkit-transform ease-out 0.1s;
    transition: transform ease-out 0.1s;
    transition: transform ease-out 0.1s, -webkit-transform ease-out 0.1s;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    white-space: nowrap;
    margin: 0;
    padding: 0;
    border: 0;
    vertical-align: baseline;
    text-align: left;
}

.input-text.focus-visible label span {
    -webkit-transform: scale(0.83333) translateY(-10px);
            transform: scale(0.83333) translateY(-10px);
}

.input-text input {
    background: #fafafa;
    background: rgba(250, 250, 250, 1);
    background: rgba(var(--b3f, 250, 250, 250), 1);
    border: 0;
    flex: 1 0 auto;
    margin: 0;
    outline: 0;
    overflow: hidden;
    padding: 9px 0 7px 8px;
    text-overflow: ellipsis;
    font-size: 16px;
    font-family: inherit;
}

.input-text.focus-visible input {
    font-size: 12px;
    padding: 14px 0 2px 8px !important;
}

.input-text input {
    outline: none;
}



