.banner {
    background-color: #ebebeb;
    display: flex;
    padding: 50px;
}

.banner.banner-mobile {
    display: block;
    padding: 20px;
}

.banner .slogans {
    padding: 20px;
    display: flex;
    flex: 1;
}

.slogans-center {
    margin-top: auto;
    margin-bottom: auto;
}

.slogans-center h1 {
    font-size: 3rem;
    margin-top: 0;
}

.slogans-center p {
    white-space: break-spaces;
}

.slogans-buttons button ~ button {
    margin-left: 15px;
}

.banner .container-image {
    padding: 20px;
    text-align: center;
    flex: 1;
    display: flex;
}

.container-image img {
    width: 100%;
    height: auto;
    margin: auto 0 auto 0;
}
